import React, { Children, cloneElement } from 'react';
import { node } from 'prop-types';
import { StaticQuery, graphql } from 'gatsby';
import GlobalStyle from './style';

const App = ({ children, ...otherProps }) => (
  <StaticQuery
    query={graphql`
      query RootQuery {
        allContentCoPost {
          edges {
            node {
              id
              title
              slug
              preview {
                source
              }
              createdAt
              content
              categories {
                id
                name
              }
              author {
                name
                image
              }
              seo {
                title
                description
                image
              }
            }
          }
        }
      }
    `}
    render={(data) => {
      const { allContentCoPost } = data;
      const { edges: posts } = allContentCoPost;

      const [childrenWithProps] = Children.map(
        children,
        child => cloneElement(
          child,
          { posts }
        )
      );

      return (
        <>
          {childrenWithProps}
          <GlobalStyle />
        </>
      );
    }}
  />
);

App.propTypes = {
  children: node.isRequired,
};

export default App;
